import isNumber from 'lodash/isNumber';
import config from '../config';

const { prefix } = config;

export function toCamel(str: string): string {
  return str.replace(/^\S/, (m) => m.toUpperCase());
}

export const isBrowser = typeof window !== 'undefined';

/**
 * è®¡ç®—å­—ç¬¦ä¸²å­—ç¬¦çš„é•¿åº¦å¹¶å¯ä»¥æˆªå–å­—ç¬¦ä¸²ã€‚
 * @param str ä¼ å…¥å­—ç¬¦ä¸²
 * @param maxCharacter è§„å®šæœ€å¤§å­—ç¬¦ä¸²é•¿åº¦
 * @returns å½“æ²¡æœ‰ä¼ å…¥maxCharacteræ—¶è¿”å›å­—ç¬¦ä¸²å­—ç¬¦é•¿åº¦ï¼Œå½“ä¼ å…¥maxCharacteræ—¶è¿”å›æˆªå–ä¹‹åçš„å­—ç¬¦ä¸²å’Œé•¿åº¦ã€‚
 */
export function getCharacterLength(str: string, maxCharacter?: number) {
  const hasMaxCharacter = typeof maxCharacter === 'number';
  if (!str || str.length === 0) {
    if (hasMaxCharacter) {
      return {
        length: 0,
        characters: str,
      };
    }
    return 0;
  }
  let len = 0;
  for (let i = 0; i < str.length; i++) {
    let currentStringLength = 0;
    if (str.charCodeAt(i) > 127 || str.charCodeAt(i) === 94) {
      currentStringLength = 2;
    } else {
      currentStringLength = 1;
    }
    if (hasMaxCharacter && len + currentStringLength > maxCharacter) {
      return {
        length: len,
        characters: str.slice(0, i),
      };
    }
    len += currentStringLength;
  }
  if (hasMaxCharacter) {
    return {
      length: len,
      characters: str,
    };
  }
  return len;
}

export const sleep = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms)); // eslint-disable-line

export const uniqueFactory = (compName: string) => {
  let number = 0;
  return () => `${prefix}-${compName}_${number++}`;
};

export const convertUnit = (val: string | number | undefined) => {
  if (val == null) return 0;
  return isNumber(val) ? `${val}px` : val;
};

export const reconvertUnit = (val: string | number | undefined) => {
  if (val == null) return 0;
  return isNumber(val) ? Number(val) : Number(val.slice(0, -2));
};

/**
 * æ ¼å¼åŒ–æ•°å­—
 * @param value ä¼ å…¥çš„æ•°å­—å­—ç¬¦ä¸²
 * @param allowDecimal æ˜¯å¦å…è®¸å°æ•°ï¼Œé»˜è®¤ä¸º true
 * @param allowNegative æ˜¯å¦å…è®¸è´Ÿæ•°ï¼Œé»˜è®¤ä¸º true
 * @returns è¿”å›æ ¼å¼åŒ–åçš„æ•°å­—å­—ç¬¦ä¸²
 */
export const formatNumber = (value: string, allowDecimal = true, allowNegative = true) => {
  if (allowDecimal) {
    const index = value.indexOf('.');
    if (index !== -1) {
      value = `${value.slice(0, index + 1)}${value.slice(index).replace(/\./g, '')}`;
    }
  } else {
    const [splitValue = ''] = value.split('.');
    value = splitValue;
  }

  if (allowNegative) {
    const index = value.indexOf('-');
    if (index !== -1) {
      value = `${value.slice(0, index + 1)}${value.slice(index).replace(/-/g, '')}`;
    }
  } else {
    value = value.replace(/-/g, '');
  }

  return value.replace(/[^\d.-]/g, '');
};
